.app{
  width: 100%;
  margin: 100px auto;
  box-shadow: 0 0 5px #ccc;
}
.details{
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  padding: 30px 0;
}
.details .big-img{
  max-width: 700px;
  min-width: 290px;
  overflow: hidden;
  margin: 25px;
}
.big-img img{
  width: 100%;
  height: 100%;
  display: block;
  object-fit: cover;
}

.details .big-img2{
  display: none;
}
.big-img2 img{
  display: none;
}

.video1 {
  margin: 25px;
}

.video2 {
  margin: 25px;
  display: none;
}

.details .box{
  max-width: 700px;
  min-width: 290px;
  margin: 25px;
}
.box .row{
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  margin-bottom: 15px;
}
.box .row h2{
  text-transform: uppercase;
  letter-spacing: 2px;
}
.box .row div .price{
  color: #83ac2b;
  font-size: 20px;
}
.box .row div .discount{
  color: #d2c1bd;
  margin-right: 10px;
  font-size: 20px;
  text-decoration: line-through;
}

.box .colors button{
  width: 30px;
  height: 30px;
  border: 1px solid #333;
  outline: none;
  margin-right: 5px;
  cursor: pointer;
  margin-top: 10px;
}
.box p{
  line-height: 1.5;
  margin: 15px 0;
}
.thumb{
  width: 100%;
  height: 100px;
  display: flex;
  cursor: pointer;
  margin: 10px 0;
}
.thumb img{
  width: 90px;
  height: 100%;
  display: block;
  object-fit: cover;
  border: 1px solid #ddd;
  margin-right: 5px;
  opacity: 0.7;
  border-radius: 5px;
}
.thumb img.active{
  opacity: 1;
  border: 1px solid lightseagreen;
}
.box .cart{
  background: #333;
  color: white;
  outline: none;
  border: none;
  cursor: pointer;
  width: 200px;
  height: 60px;
  padding: 10px 25px;
  margin-top: 15px;
  border-radius: 10px;
  font-size: 20px;
}

.formModal .rowForm .label {
  display: flex;
  flex-direction: row;
}

.formModal .rowForm .label .reddot {
  color: red;
}

.formModal .rowFormRequie .reddot {
  color: red;
  margin-right: 10px;
}

.formModal .titleForm {
  text-align: center;
  margin-bottom: 90px;
}

.formModal .rowForm {
  margin-bottom: 20px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
}

.formModal .rowFormRequie {
  margin-bottom: 20px;
  display: flex;
  flex-direction: row;
  align-items: center;
  color: red;
}

.formModal .rowForm .label {
  flex: 2;
}

.formModal .rowForm .inputForm {
  flex: 3;
  height: 35px;
  border-radius: 8px;
  padding-left: 10px;
}

.footerForm {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
}

.footerForm .buttonCancel {
  width: 100px;
  height: 40px;
  border-radius: 8px;
}

.footerForm .buttonSubmit {
  width: 100px;
  height: 40px;
  background-color: aqua;
  border-radius: 8px;
  color: #fff;
  border-color: aqua;
}
.footerForm .buttonSubmitDisable {
  width: 100px;
  height: 40px;
  border: 1px solid #b7b7b7;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 8px;
  font-size: 13px;
  background-color: #b7b7b7;
  color: #fff;
}

